<template lang="html">

  <h1>Baza Korisnika i Kompanija</h1>

  <TabNav :tabs="companyList" :selected="selected"  @selected="setSelected" id="companyNav">
    <Tab v-for="(company,i) in companyList" :key="company" :isSelected="selected === company ">

      <div class="validation">
        {{validation}}
      </div>
      <div class="container" v-for="(userX,j) in listOfListsOfUsers[i]"  :key="userX._id" >
        <table>
          <tr>
            <th>Ime</th>
            <th>Email</th>
            <th>Username</th>
            <th>Sektor</th>
            <th>Radno mesto</th>
            <th>Nadređeni</th>
            <th>Izmeni nadređenog</th>
          </tr>
          <tr>
            <td>{{userX.name}}</td>
            <td>{{userX.email}}</td>
            <td>{{userX.username}}</td>
            <td>{{userX.sektor}}</td>
            <td>{{userX.radnoMesto}}</td>
            <td>{{userX.nadredjeni}}</td>
            <td>
              <select name="collegue" id="collegue" v-model="collegue[j]" >
                 <option  v-for="collegue in usersSameCompany" v-bind:key="collegue.name">{{collegue.name}}</option>
              </select>
            </td>
          </tr>

        </table>
        <button type="button" name="button" @click="removeUser(userX._id)">Ukloni Zaposlenog</button>
        <button type="button" name="button" @click="updateUser(userX._id,collegue[j])">Sacuvaj izmene</button>

        <input type="text" name="" v-model="pass[j]">
        <button type="button" name="button" @click="changePassThis(userX.username,pass[j])">Izmeni password</button>

      </div>
    </Tab>
  </TabNav>

</template>

<script>
// @ is an alias to /src
// eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';
// eslint-disable-next-line no-unused-vars
import TabNav from '@/components/TabNav.vue'
import Tab from '@/components/Tab.vue'
export default {
  computed: mapGetters(['companys',"users","user",'usersSameCompany']),
  components:{
    TabNav,
    Tab,
  },
  data(){
    return{
      selected: '',
      companyList:[],
      //each element is a list of users (different lists are for different companies)
      listOfListsOfUsers:[],
      validation:"",
      tester:"",
      collegue:[],
      pass:[],
    }
  },
  methods: {
    ...mapActions(['getCompanies','getAllUsers','getProfile','deleteUser','getCollegues','setNadredjeni','changePass']),
    async setSelected(tab){
      this.selected=tab;
      await this.getCollegues(tab);

    },
    changePassThis(usrName,pass){
      let data={
        username:usrName,
        password:pass,
      }
      this.changePass(data);
      this.validation="Zamenjena šifra za username: "+usrName;
    },
    removeUser(userID){
      this.validation="";
      this.deleteUser(userID);
      this.validation=`Uklonjen Zaposleni id=${userID}`;
    },
  async updateUser(userID, collegueName){
      console.log(userID);
      console.log(collegueName);
      let nadredjeni_id;
      for (var i = 0; i < this.users.length; i++) {
        if(this.users[i].name == collegueName){
          nadredjeni_id= this.users[i]._id;
        }
      }
      let nadredjeniData={
        userID:userID,
        nadredjeniName:collegueName,
        nadredjeni_id:nadredjeni_id
      }
      console.log("NADREDJENI ID: ");
      console.log(nadredjeni_id);
      await this.setNadredjeni(nadredjeniData);
      this.validation=`Zaposlenom sa ID=${userID} postavljen nadredjeni ${collegueName}`;
    }
  },
  async created() {
    await this.getProfile();
    await  this.getCompanies();
    await this.getAllUsers();


    //Fill list of companies (companyList is used by Tabs)
    this.companys.forEach(company => {
      let list = [];
      this.listOfListsOfUsers.push(list);
      if(company.name!="admin"){
      this.companyList.push(company.name);
      }
    });
    for (let i = 0; i < this.listOfListsOfUsers.length; i++) {
      let companyName = this.companyList[i];
      if(companyName!="admin"){
      for(let j = 0; j< this.users.length; j++){
        if(this.users[j].type === companyName){
          this.listOfListsOfUsers[i].push(this.users[j]);
        }
      }
    }
    }
  },
}
</script>

<style lang="css" scoped>

table{
  border: 2px solid black;
  text-align: center;
}
td, th{
  text-align: center;
  border: 1px solid black;
}
tr:nth-child(even){background-color: #aaaacc;}
tr:nth-child(odd){background-color: #ccccff;}
tr:hover {background-color: #afa;}

.container{
  margin-top: 10px;
}

.validation{
  margin-top: 5px;
  background-color: #00aaaa;
}

</style>
